.wrapperDiv {
  display: grid;
  grid-template-columns: auto 120px;
  text-align: left;
  text-indent: 50px;
  /* justify-content: space-around; */
}

.wrapperDiv div{
  padding: initial;
  text-align: right;
  position: relative;
  right: 70px;
}

.right {
  display: block;
  position: relative;
  right: 70px;
  
}