.App {
  text-align: center;
  background-color: #f3f3f3;
  color: blac
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}



.App-header {
  background-color: #dfe5f0;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

.App-link {
  color: #61dafb;
}

.wrapper {
  display: grid;
  grid-template-columns: auto auto;
}

.inputs {
  
  /* width: 200px; */
  display: grid;
  grid-template-columns: auto auto;
  justify-content: right;
  /* height: 200px; */
  /* border: 1px solid red; */
  padding: 8px;
  font-size: 1.4em;
}
.inputsColumn {
  display: grid;
  grid-template-columns: auto;
}
.answers {
  /* width: 200px; */
  /* height: 200px; */
  /* border: 1px solid green; */
  padding: 8px;
  font-size: 1.4em;
  
}

input {
  text-align: center;
}

input[type=number]::-webkit-inner-spin-button {
  display: inline-block;
  position: relative;
  left: -4px;
  /* opacity: .6; */
  transform: rotate(90deg) scale(2) ;
}

footer span {
  display: inline-block;
  position: relative;
  transform: translateX(180px);
}
  